import server from '../../server.js';
//import uniFab from '@/components/uni-fab.vue';
import uniLoadMore from '@/components/uni-load-more.vue';
export default {
	components: {  
		uniLoadMore,
	},
	data() {
		return {
			Dindex:[],
			CustomBar: this.CustomBar,
			modalName: null,
			AvatarUrl:"",
			TabCur: 0,
			gaodu:0,
			msgNumber:0,
			weikong:true,
			username:"游客",
			page:1,
			postList:[],
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			status: 'loading',
			statusTypes: [{
				value: 'more',
				text: '加载前',
				checked: true
			}, {
				value: 'loading',
				text: '加载中',
				checked: false
			}, {
				value: 'noMore',
				text: '我是有底线的',
				checked: false
			}],
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			},
			zhongcao:"0",
			guanzhu:"0",
			userid:"0",
			fensi:"0",
			dengji:"0",
			index:false,
			userInfo:[]
		}
	},
	onShow:function(){
		uni.request({
			method:'GET',
			url: "https://api.angeli.top/user.php?type=getMyNoRead", //仅为示例，并非真实接口地址。
			data: {
				auid:this.userid
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded',
				'Cookie':server.cookie
			},
			success: (res) => {
				console.log(res)
				if(res.data.code=="1"){
					this.msgNumber=res.data.data.count
				}else{
		
				}
				console.log(this.msgNumber)
			},
			complete() {
				
			}
		});
	},
	onLoad:function(e){
		
		console.log(e)
		if(e.id==1){
			
			this.index=true;
		}
		if(e.type=='plusPost'){
			this.getPostData('new',0);
		}


















		//上面只在APP，下面仅在微信小程序

		if(server.userinfo.Auid==""||server.userinfo.Auid==null){
			uni.login({
			    provider: 'weixin',
			    success: (res) => {
					console.log(res);
					uni.request({
						method:'POST',
						url: 'https://api.angeli.top/reg.php?type=wxlogin', //仅为示例，并非真实接口地址。
						data: {
							code: res.code
						},
						header: {
							'content-type': 'application/x-www-form-urlencoded'
						},
						success: (res) => {
							console.log(res);
							if(res.data.code=="0"){
								server.usersk=res.data.data.session_key
								uni.showToast({
									title: "欢迎你，游客",
									position:'bottom',
									icon:'none'
								});
								this.AvatarUrl="https://sz.oss.data.angeli.top/angeli-image/1562320238188110.png";
							}else if(res.data.code=="1" || res.data.code=="2"){
								this.AvatarUrl=res.data.data.AvatarUrl;
								this.username=res.data.data.UserName;
								this.zhongcao=res.data.data.ZhongcaoCount;
								this.guanzhu=res.data.data.FollowedCount;
								this.fensi=res.data.data.FollowerCount;
								this.dengji=res.data.data.Rank;
								this.userid=res.data.data.Auid;
								this.userInfo=res.data.data;
								server.userinfo=res.data.data;
								server.cookie=res.header['Set-Cookie'];
								if(res.data.code=="2"){
									uni.showToast({
										title: res.data.msg,
										position:'bottom'
									})
								}else{
									uni.showToast({
										title: '欢迎你，'+this.username,
										position:'bottom'
									})
								}
								this.getPostData('new',0);
							}
							console.log("———————————用户信息——————————");
							console.log(server.userinfo)
						},
						fail:(src) =>{
							uni.showToast({
								title: "未知原因，登录失败！",
								position:'bottom',
								icon:'none'
							})
						},
					});
			    },
			    fail: (err) => {
					uni.showToast({
						title: "登录失败呢！关闭之后重新打开吧！",
						position:'bottom'
					}),
			        console.error('授权登录失败：' + JSON.stringify(err));
			    }
			})
		}

	},
	onReady: function() {
		this.getHei()
		this.postList=[];
		
	},
	methods: {
		getFriend:function(){
			uni.navigateTo({
				url: '../menu/friend'
			})
		},
		getbieren:function(e){
			uni.navigateTo({
				url: '../i/bieren?auid='+e
			})
		},
		getShoucang:function(){
			uni.navigateTo({
				url: '../menu/shoucang'
			})
		},
		getJifen:function(){
			uni.navigateTo({
				url: '../menu/jifen'
			})
		},
		getMessage:function(){
			uni.navigateTo({
				url: '../menu/Message'
			})
		},
		wallet:function(){
			uni.navigateTo({
				url: '../menu/wallet'
			})
		},
		set:function(){
			uni.navigateTo({
				url: '../set/set'
			})
		},
		logout:function(){
			uni.clearStorage();
			uni.showToast({
				title: "已退出登录",
				position:'bottom',
				icon:'none',
				position:'center',
			})
			server.userinfo=[];
			server.cookie="";
			this.modalName = null

		},
		cs:function(e){
			this.old.scrollTop = e.detail.scrollTop
		},
		sousuo:function(){
			uni.navigateTo({
				url: '../sousuo/sousuo'
			})
		},
		getClass:function(id){
			uni.navigateTo({
				url: '../classPost/classPost?id='+id
			})
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
			this.AvatarUrl=server.userinfo.AvatarUrl;
			this.username=server.userinfo.UserName;
			this.zhongcao=server.userinfo.ZhongcaoCount;
			this.guanzhu=server.userinfo.FollowedCount;
			this.fensi=server.userinfo.FollowerCount;
			this.dengji=server.userinfo.Rank;
			this.userid=server.userinfo.Auid;
			console.log("测试！：",this.username)
			
			
			//请求 https://api.angeli.top/user.php?type=getMyNoRead
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=getMyNoRead", //仅为示例，并非真实接口地址。
				data: {
					auid:this.userid
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						this.msgNumber=res.data.data.count
					}else{

					}
					console.log(this.msgNumber)
				},
				complete() {
					
				}
			});
		},
		hideModal(e) {
			this.modalName = null
		},
		tabSelect(e) {
			this.TabCur = e
			this.scrollTop = this.old.scrollTop
			this.$nextTick(function() {
				this.scrollTop = 0
			});
			console.log(e)
			if(e==0){ //最新
				this.getPostData('new',0);
			}else if(e==1){ //最热
				this.getPostData('hot',0);
			}else if(e==2){ //关注
				this.getPostData('guanzhu',0);
			}
			
		},
		Like:function(postid,auid,give,index){
			if(give===true){
				var modea='del'
			}else{
				var modea='add'
			}
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/post.php?type=Like", //仅为示例，并非真实接口地址。
				data: {
					fuid: auid,
					postid:postid,
					mode:modea
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					if(res.data.code=="1"){
						if(modea=='add'){
							this.postList[index].Give=true;
							uni.showToast({
								title: "种草成功！",
								position:'bottom',
								icon:'none'
							});
						}else{
							this.postList[index].Give=false;
							uni.showToast({
								title: "取消种草成功！",
								position:'bottom',
								icon:'none'
							});
						}
						
						this.$forceUpdate()
					}else{
						
						uni.showToast({
							title: "种草失败！",
							position:'bottom',
							icon:'none'
						});
					}
				},
				complete() {
					
				}
			});
		},
		caidan:function(res){
			console.log(res)
			this.Dindex=res;
			uni.showActionSheet({
				itemList: ['分享给朋友', '生成海报', '举报'],
				success: function (res) {
					if(res.tapIndex==0){
						uni.showToast({
							title: "分享"+res.Content,
							position:'bottom',
							icon:'none'
						});
					}
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			});
		},
		getHei(){
			
			let view = uni.createSelectorQuery().select("#topbox");
			view.fields({
					  size: true,
					  scrollOffset: true
					}, data => {
					  this.gaodu=data.height-5;
					  console.log("总高度：" + this.gaodu);
					}).exec();
		},
		showImage(res,c){
			uni.previewImage({
				current:c,
				urls: res,
				longPressActions: {
				itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function (res) {
						
						uni.showToast({
							title: '选中了第' + (res.tapIndex + 1) + '个按钮',
							position:'bottom',
							icon:'none'
						});
						console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
						
					},
					fail: function (res) {
						console.log(res.errMsg);
					}    
				}
			})
		},
		getPostData(type,classId){
			uni.showLoading({
				title: '获取数据中'
			});
			console.log(type)
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/post.php?type=outPostList', //仅为示例，并非真实接口地址。
				data: {
					page: 0,
					postType:type,
					count:10,
					classId:classId
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					uni.hideLoading();
					this.postList=[]
					console.log(res.data.data);
					console.log("————————————帖子列表——————————");
					this.postList=res.data.data;
					console.log(this.postList);
					this.weikong=false
					if(res.data.code!=="1"){
						uni.showToast({
							title: '获取帖子失败，建议重启',
							position:'bottom',
							icon:'none'
						});
					}
					if(res.data.data==false){
						this.weikong=true
					}
					//this.$forceUpdate();
				},
				complete:function(){
					uni.hideLoading();
				},
				
			});
			
		},
		reguser:function(){
			if(server.userinfo.Auid==""||server.userinfo.Auid==null){
				uni.navigateTo({
					url: '../reg/reg'
				})
			}else{
				uni.navigateTo({
					url: '../i/i'
				})
			}
			
			
		},
		getVip:function(){
			uni.navigateTo({
				url: '../vip/vip'
			})
		},
		pluspost:function(){
			console.log("发帖：",server.userinfo.Auid)
			if(server.userinfo.Auid==""||server.userinfo.Auid==null){
				uni.showToast({
					title: "你还没有登录，请登录后再来吧",
					position:'bottom',
					icon:'none'
				});
				setTimeout(function () {
					uni.navigateTo({
						url: '../reg/reg'
					})
				}, 1200);
			}else{
				uni.redirectTo({
					url: '../post/post'
				})
			}
			
		},
		EndLoding:function(e){
			this.page++;
			console.log(this.scrollTop)
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/post.php?type=outPostList', //仅为示例，并非真实接口地址。
				data: {
					page: this.page,
					posttype:"1",
					sort:'PsotDate',
					count:10
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					console.log("————————————帖子列表——————————"+this.page);
					if(res.data.data.length==undefined){
						this.page--;
						this.status ="noMore";
					}else{
						this.postList=this.postList.concat(res.data.data);
						this.status ="loading";
						console.log(this.postList);
					}
					
					
				}
			});
		},
		getpostinfo:function(id){
			uni.navigateTo({
				url: '../postinfo/postinfo?id='+id
			});
		}
	},
	onShareAppMessage(res) {
		if (res.from === 'button') {// 来自页面内分享按钮
		  console.log(res.target)
		} 
		return {
		  title: this.postInfo.Content,
		  path:'/pages/postinfo/postinfo?id='+this.postid,
		  desc:this.postInfo.Content,
		  imageUrl:this.postInfo.PictureId[0]
		}
	}
}

